import React, { Component } from "react";
import {
  HeaderDiv,
  HeaderItem,
  HeaderLinks,
  MainHeader,
  ToggleIcon,
  HeaderTitle,
} from "./HeaderComponents";
import { FaBars } from "react-icons/fa";
import { BrowserRouter as Router, Link } from "react-router-dom";
export class Header extends Component {
  render() {
    return (
      <>
        <HeaderDiv>
          <MainHeader>
            <ToggleIcon onClick={this.props.toggle}>
              <FaBars onClick={this.props.toggle}></FaBars>
            </ToggleIcon>
            <HeaderTitle>Header</HeaderTitle>
          </MainHeader>

          <HeaderLinks>
            <Router>
              <HeaderItem href="#home">Home</HeaderItem>
              <HeaderItem href="#about">About</HeaderItem>
              <HeaderItem href="#project">Project</HeaderItem>
              <HeaderItem href="#contact">Contact</HeaderItem>
            </Router>
          </HeaderLinks>
        </HeaderDiv>
      </>
    );
  }
}

export default Header;
