import styled from "styled-components";

export const ProjectDiv = styled.div`
  align-items: center;
  height: fit-content;
  text-align: center;
  color:white;
  background-color: #003747;
`;
export const ProjectHeader = styled.div`
  font-size: 4rem;
  padding-top: 5rem;
  cursor: context-menu;
  &:after {
    background: none repeat scroll 0 0 transparent;

    content: "";
    display: block;
    height: 2px;
    left: 50%;

    position: absolute;
    background: #fff;
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
  }
  &:hover:after {
    width: 60%;
    left: 18%;
  }
`;
export const ProjectSubContent = styled.div`
  font-size: small;
  cursor: context-menu;
  padding: 2rem 3rem 3rem 3rem;
`;
