import React, { Component } from "react";
import { FaTimes } from "react-icons/fa";
import {
  SidebarDiv,
  SidebarItem,
  SidebarItems,
  ToggleIcon,
} from "./SideBarComponents";

export class SideBar extends Component {
  render() {
    return (
      <>
        <SidebarDiv isOpen={this.props.isOpen}>
          <SidebarItems>
            <ToggleIcon>
              <FaTimes onClick={this.props.toggle}></FaTimes>
            </ToggleIcon>
            <SidebarItem href="#home" onClick={() => this.props.toggle}>
              Home
            </SidebarItem>
            <SidebarItem href="#about" onClick={() => this.props.toggle}>
              About
            </SidebarItem>
            <SidebarItem href="#project" onClick={() => this.props.toggle}>
              Project
            </SidebarItem>
            <SidebarItem href="#contact" onClick={() => this.props.toggle}>
              Contact
            </SidebarItem>
          </SidebarItems>
        </SidebarDiv>
      </>
    );
  }
}

export default SideBar;
