import React, {useState} from 'react'
import Header from './Header'
import SideBar from './SideBar'

export const Navbar = () => {
    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => [setIsOpen(!isOpen), console.log("works bro")];

    const propy = "pass";
    console.log(toggle)
    return (
        <>
            <Header toggle={toggle} pass={propy} isOpen={isOpen}/>
            <SideBar toggle={toggle} pass={propy} isOpen={isOpen}/>
        </>
    )
}
